/** @jsx jsx */
import { memo, useMemo, useEffect, useState } from 'react';
import { jsx, css, keyframes } from '@emotion/core';
import Typist from 'react-typist';
import logo from '../../logo.svg';

const transform = keyframes`
  0% {
    transform: scale(0.2);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
`;
const rotate = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`;

const styles = {
  icon: css`
    height: 50vmin;
    pointer-events: none;
    animation: ${transform} 3s ease-in 0s 1, ${rotate} 20s linear 3s infinite;
  `
};

const Logo = () => {
  const [isCursorShow, setIsCursorShow] = useState(false);
  const cursorOptions = useMemo(
    () => ({ show: isCursorShow, hideWhenDone: true, hideWhenDoneDelay: 0 }),
    [isCursorShow]
  );
  useEffect(() => {
    setTimeout(() => {
      setIsCursorShow(true);
    }, [3000]);
  }, []);
  return (
    <div>
      <img src={logo} css={styles.icon} alt="logo" />
      <Typist cursor={cursorOptions} startDelay={3000}>
        I am Andy Lee.
      </Typist>
    </div>
  );
};

export default memo(Logo);
