/** @jsx jsx */
import { memo } from 'react';
import { jsx, css } from '@emotion/core';

const styles = {
  container: css`
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 12px;
    justify-items: start;
    margin-bottom: 12px;
  `,
  source: css`
    font-size: calc(10px + 0.5vmax);
    @media screen and (max-width: 375px) {
      font-size: 8px;
    }
    font-style: italic;
    color: gray;
  `
};

const Block = ({ name, devEnv, lib = '', items = [] }) => {
  return (
    <div css={styles.container}>
      <b>{`${name} - ${devEnv} ${lib === '' ? '' : `- ${lib}`}`}</b>
      {items.map(item => (
        <div key={item.name}>
          - {item.name} <span css={styles.source}>({item.source})</span>
        </div>
      ))}
    </div>
  );
};

export default memo(Block);
